
html,
body {
  height: 100%;
}
.song-bg {
  position: fixed;
  top: 0;
  bottom: 0;
  width: 100%;
  background-position: bottom center;
  background-size: cover;
  background-repeat: no-repeat;
  z-index: 1;
  -webkit-filter: blur(15px);
  -webkit-transform: scale(1.15);
}
.song-mask {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  z-index: 2;
  width: 100%;
  background-color: #000;
  opacity: 0.6;
}
.song_info {
  position: fixed;
  width: 100%;
  box-sizing: border-box;
  z-index: 3;
  color: #fff;
  padding: 0.75rem;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  background-color: rgba(0, 0, 0, 0.1);
}
.song_info .album-cover-wrap {
  width: 4rem;
  height: 4rem;
  margin-right: 0.75rem;
}
.song_info .album-cover {
  width: 4rem;
  height: 4rem;
  border-radius: 50%;
  transform: rotate(0deg);
  transition: transform 200ms;
}
.song_info .info {
  -ms-flex: 1;
      flex: 1;
  width: 0;
  font-size: 0.7rem;
  line-height: 1.35rem;
}
.song_info .info h1 {
  font-size: 0.9rem;
  font-weight: normal;
}
.song_info .icon {
  display: block;
  width: 2.1rem;
  height: 2.1rem;
  margin-left: 0.5rem;
  background: url(../assets/list_sprite.png) no-repeat;
  background-size: 100%;
  -ms-flex: none;
      flex: none;
}
.song_info .play-icon {
  background-position: 0 -2.2rem;
}
.song_info .pause-icon {
  background-position: 0 0;
}
.lyc-wrap {
  position: fixed;
  top: 5.5rem;
  bottom: 8rem;
  width: 100%;
  z-index: 10;
  color: rgba(255, 255, 255, 0.6);
  text-align: center;
  line-height: 2rem;
  font-size: 0.8rem;
  -webkit-box-flex: 1;
  overflow: hidden;
  display: -ms-flexbox;
  display: flex;
  display: -webkit-box;
  -webkit-box-align: center;
}
.rotateAnim {
  animation: imgRorate 4s infinite linear;
}
.controller {
  position: fixed;
  bottom: 0;
  z-index: 11;
  width: 100%;
  box-sizing: border-box;
  background: rgba(0, 0, 0, 0.4);
}
@keyframes imgRorate {
0% {
    transform: rotate(0deg);
}
50% {
    transform: rotate(180deg);
}
100% {
    transform: rotate(360deg);
}
}



