
.lyc {
  height: 18rem;
  width: 100%;
  overflow: hidden;
  position: relative;
}
.lyc .lyc-wrap-inner {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  transition: top 350ms;
}
.lyc .lyc-wrap-inner p {
  padding: 0 1rem;
  box-sizing: border-box;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.lyc-active {
  color: #31c27c;
}



