
.progress-wrap {
  display: -ms-flexbox;
  display: flex;
  color: #808080;
  font-size: 0.7rem;
  margin-bottom: 1.2rem;
}
.progress-wrap .time {
  text-align: center;
  width: 18%;
}
.play-progress {
  height: 2px;
  width: 0;
  position: relative;
  top: 0.5rem;
  background-color: rgba(255, 255, 255, 0.2);
  -ms-flex: 1;
      flex: 1;
}
.play-progress .bar-bg {
  background-color: #31c27c;
  position: absolute;
  left: 0;
  top: 0;
  height: 2px;
  transition: width 50ms;
  /* 如何动画更平滑 */
}
.play-progress .bar-bg:after {
  content: '';
  position: absolute;
  right: -2px;
  top: -2px;
  height: 6px;
  width: 6px;
  border-radius: 50%;
  background-color: #31c27c;
  box-shadow: 0 0 4px #31c27c;
}
.active b {
  position: absolute;
  right: 0.75rem;
  top: 0.5rem;
  width: 0.5rem;
  height: 0.6rem;
  overflow: hidden;
}
.active i {
  float: left;
  width: 1px;
  height: 0.6rem;
  margin-right: 0.1rem;
  background-color: #31c27c;
  animation: upAndDown 4s infinite linear;
}
.active i:nth-of-type(1) {
  -webkit-animation-duration: 800ms;
}
.active i:nth-of-type(2) {
  -webkit-animation-duration: 1200ms;
}
.active i:nth-of-type(3) {
  -webkit-animation-duration: 1400ms;
}
.active i:nth-of-type(4) {
  margin-right: 0;
  -webkit-animation-duration: 1000ms;
}
@keyframes upAndDown {
0% {
    transform: translateY(0);
}
15% {
    transform: translateY(0.2rem);
}
35% {
    transform: translateY(0.4rem);
}
50% {
    transform: translateY(0.6rem);
}
65% {
    transform: translateY(0.4rem);
}
85% {
    transform: translateY(0.2rem);
}
100% {
    transform: translateY(0);
}
}
.controller-wrap .row {
  display: -ms-flexbox;
  display: flex;
}
.controller-wrap .row .icon {
  width: 1.5rem;
  height: 1.5rem;
  display: inline-block;
  margin: 0.75rem 1rem;
  background-image: url(https://y.gtimg.cn/mediastyle/mobile/app/share/img/sprite_play.png);
  background-size: 160%;
}
.controller-wrap .row .icon-prev,
.controller-wrap .row .icon-next {
  -ms-flex: 1;
      flex: 1;
  width: 0;
  background-image: none;
}
.controller-wrap .row .icon-list {
  background-position: 0 -14.1rem;
}
.controller-wrap .row .icon-fav {
  background-position: 0 0.2rem;
}
.controller-wrap .songList {
  color: #fff;
  font-size: 0.7rem;
  max-height: 13.8rem;
  padding: 0.5rem 0.75rem 0.75rem;
  background-color: rgba(0, 0, 0, 0.55);
}
.controller-wrap .songList p {
  line-height: 2rem;
  font-size: 0.9rem;
  position: relative;
  text-align: center;
  border-bottom: 1px solid rgba(255, 255, 255, 0.3);
}
.controller-wrap .songList p span {
  color: rgba(255, 255, 255, 0.6);
  font-size: 0.75rem;
}
.controller-wrap .songList .icon-back {
  position: absolute;
  right: 0rem;
  top: 0.4rem;
  width: 1rem;
  height: 1rem;
}
.controller-wrap .songList .icon-back:before,
.controller-wrap .songList .icon-back:after {
  position: absolute;
  content: '';
  right: 0;
  top: 50%;
  width: 100%;
  height: 1px;
  background-color: #fff;
  transform: rotate(45deg);
}
.controller-wrap .songList .icon-back:after {
  transform: rotate(-45deg);
}
.controller-wrap .songList li {
  line-height: 2rem;
  border-bottom: 1px solid rgba(255, 255, 255, 0.15);
}
.controller-wrap .songList .active {
  color: #31c27c;
  position: relative;
}



