
.search-content {
  font-size: 0.6rem;
  background-color: #fff;
}
.search-content li {
  border-bottom: 1px solid #e5e5e5;
  padding: 0.4rem;
}
.search-content li a {
  color: #808080;
}
.search-content .flex-box {
  display: -ms-flexbox;
  display: flex;
}
.search-content .flex-icon {
  width: 2rem;
  height: 2rem;
  margin-right: 0.6rem;
  border-radius: 50%;
  overflow: hidden;
}
.search-content .flex-icon img {
  width: 100%;
}
.search-content .icon-music {
  background: url(../assets/search_sprite.png) left top no-repeat;
  background-size: cover;
  width: 1.1rem;
  height: 1rem;
  display: block;
  margin: 0.5rem 0 0 0.5rem;
}
.search-content .music-info {
  -ms-flex: 1;
      flex: 1;
  border-radius: 0;
}
.search-content h3 {
  font-size: 0.8rem;
  color: #000;
  font-weight: normal;
}



