
.nowrap {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.song-list {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  font-size: 0.9rem;
  height: 3rem;
  color: #fff;
  position: relative;
}
.song-list .song-list-no {
  -ms-flex: none;
      flex: none;
  width: 1.5rem;
  margin-right: 0.5rem;
  margin-top: -0.5rem;
  text-align: center;
  font-weight: normal;
  box-sizing: border-box;
  font-size: 0.85rem;
}
.song-list:after {
  content: '';
  position: absolute;
  left: 0;
  bottom: 0;
  right: 0;
  height: 1px;
  margin-left: 2rem;
  background-color: rgba(255, 255, 255, 0.15);
  -webkit-transform: scaleY(0.5);
}
.song-list .song-list-cont {
  -ms-flex: 1;
      flex: 1;
  width: 0;
  color: #fff;
  display: block;
}
.song-list .song-info {
  font-size: 0.7rem;
  vertical-align: middle;
  color: rgba(255, 255, 255, 0.6);
}
.song-list .song-info .icon-point {
  display: inline-block;
  width: 2px;
  height: 2px;
  border-radius: 50%;
  background-color: #fff;
  margin: 0 4px;
  vertical-align: middle;
}



