
.rel {
  position: relative;
}
.top-img-wrap {
  -ms-flex: none;
      flex: none;
}
.top-img-wrap .top-img {
  width: 5rem;
  height: 5rem;
  margin-right: 0.5rem;
  display: block;
}
.top-img-wrap p {
  position: absolute;
  left: 0.5rem;
  bottom: 0;
  font-size: 0.6rem;
  color: #fff;
}
.rank-wrap {
  padding: 0.5rem;
  background-color: #f4f4f4;
}
.rank-wrap a {
  margin-bottom: 0.5rem;
  background-color: #fff;
  display: -ms-flexbox;
  display: flex;
  font-size: 0.7rem;
  line-height: 1.5em;
  color: #000;
}
.rank-wrap a:last-child {
  margin: 0;
}
.rank-wrap a .top-img-wrap p {
  opacity: 0.6;
}
.rank-wrap a .top-summary h3 {
  font-size: 0.8rem;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  line-height: 1.8em;
  margin-bottom: 0.3rem;
}
.rank-wrap a .top-summary p {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.rank-wrap a .top-summary p span {
  color: rgba(0, 0, 0, 0.5);
}
.rank-wrap a .top-summary .icon-arrow {
  position: absolute;
  right: 10px;
  top: 50%;
  margin-top: -4px;
  width: 6px;
  height: 6px;
  border-right: 1px solid #b2b2b2;
  border-bottom: 1px solid #b2b2b2;
  -webkit-transform: rotate(-45deg);
}
.rank-wrap .top-summary {
  -ms-flex: 1;
      flex: 1;
  width: 0;
  padding-right: 0.6rem;
}
.link2client {
  display: block;
  height: 1.8rem;
  line-height: 1.8rem;
  color: rgba(0, 0, 0, 0.6);
  font-size: 0.7rem;
  text-align: center;
  background-color: #f4f4f4;
  margin-bottom: 0;
}



