
.header .header-bg {
  width: 100%;
  display: block;
}
.header .album-mask {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  padding-top: 60%;
  height: 0;
  z-index: 2;
  background-image: -webkit-linear-gradient(top, transparent, currentColor 80%);
}
.header .album-info {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 3;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  margin: 0 15px 25px;
  color: #fff;
}
.header .album-info .album-title {
  -webklit-flex: 1;
  -ms-flex: 1;
      flex: 1;
  width: 0;
  line-height: 1.5em;
}
.header .album-info .album-title h1 {
  font-weight: normal;
  font-size: 1.2rem;
}
.header .album-info .album-title p {
  font-size: 0.7rem;
}
.header .album-info .album-play {
  margin-left: 0.6rem;
  width: 2rem;
  height: 2rem;
  background: url(../../assets/list_sprite.png);
  background-size: cover;
  cursor: pointer;
}
.list {
  padding: 0 0.5rem;
  margin: 0;
}



