
.search-wrap {
  background-color: #f4f4f4;
  display: -ms-flexbox;
  display: flex;
  padding: 0.5rem;
}
.search-wrap form {
  background-color: #fff;
  position: relative;
  padding-left: 1rem;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex: 1;
      flex: 1;
  padding: 8px 12px 8px 35px;
  transition: all 200ms;
}
.search-wrap .key-word {
  border: 0 none;
  padding: 0;
  -ms-flex: 1;
      flex: 1;
  outline: none;
  height: 1rem;
  line-height: 1rem;
}
.search-wrap .icon-search {
  position: absolute;
  top: 8px;
  left: 8px;
  width: 1rem;
  height: 1rem;
  background: url(data:image/png;base64,) no-repeat;
  background-size: 100%;
}
.search-wrap .icon-del {
  position: absolute;
  top: 8px;
  right: 0.5rem;
  width: 1rem;
  height: 1rem;
  border-radius: 50%;
  background-color: #b1b1b1;
}
.search-wrap .icon-del::before,
.search-wrap .icon-del:after {
  content: '';
  position: absolute;
  top: 50%;
  left: 15%;
  width: 70%;
  height: 1px;
  background-color: #fff;
}
.search-wrap .icon-del:before {
  transform: rotate(-45deg);
}
.search-wrap .icon-del:after {
  transform: rotate(45deg);
}
.search-wrap .search-btn {
  font-size: 0.7rem;
  line-height: 36px;
  padding: 0 10px;
  color: #555;
}
.hot-keys {
  background: #fff;
  padding: 15px 15px 10px 15px;
  font-size: 0.65rem;
}
.hot-keys .hot-title {
  line-height: 1.2em;
  margin-bottom: 0.5rem;
  color: rgba(0, 0, 0, 0.6);
}
.hot-keys span {
  color: #000;
  display: inline-block;
  height: 1.5rem;
  line-height: 1.5rem;
  border: 1px solid rgba(0, 0, 0, 0.6);
  border-radius: 0.75rem;
  word-break: keep-all;
  padding: 0 10px;
  margin: 0 0.65rem 0.5rem 0;
}
.hot-keys span.highlight {
  border-color: #fc4524;
  color: #fc4524;
}
.searchHis {
  color: #000;
  font-size: 0.7rem;
}
.searchHis li {
  background-color: #fff;
  height: 2.2rem;
  line-height: 2.2rem;
  padding: 0 0.8rem;
  border-bottom: 1px solid #e3e3e3;
}
.searchHis li:last-child {
  border: none;
  margin-top: -1px;
}
.searchHis .clearHis {
  text-align: center;
  color: #47c88a;
  font-size: 0.6rem;
}



