
.head {
  background-color: #fff;
  height: 2.25rem;
  line-height: 2.25rem;
  font-size: 0.8rem;
  color: #fff;
  text-align: center;
  position: relative;
  display: -ms-flexbox;
  display: flex;
}
.head .active {
  position: relative;
  color: #31c27c;
}
.head .active:after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: #31c27c;
}
.head a {
  -ms-flex: 1;
      flex: 1;
  width: 0;
  color: rgba(0, 0, 0, 0.6);
}
.head a span {
  display: block;
}



