
.flex-box {
  display: -webklit-flex;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
}
.flex-body {
  -ms-flex: 1;
      flex: 1;
  -webkit-box-pack: center;
}
.title {
  font-size: 0.9rem;
  padding: 1.25rem 0;
  line-height: 1.2rem;
  text-align: center;
}
.intro {
  padding: 0 0.75rem 3rem;
  font-size: 0.7rem;
  line-height: 1.1rem;
  text-align: justify;
}
.header .header-bg {
  width: 100%;
  display: block;
}
.header .album-mask {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  padding-top: 60%;
  height: 0;
  z-index: 2;
  background-image: -webkit-linear-gradient(top, transparent, currentColor 80%);
}
.header .album-info {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 3;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  margin: 0 15px 25px;
  color: #fff;
}
.header .album-info .album-title {
  -webklit-flex: 1;
  -ms-flex: 1;
      flex: 1;
  width: 0;
  line-height: 1.5em;
}
.header .album-info .album-title h1 {
  font-weight: normal;
  font-size: 1.2rem;
}
.header .album-info .album-title p {
  font-size: 0.7rem;
}
.header .album-info .album-play {
  margin-left: 0.6rem;
  width: 2rem;
  height: 2rem;
  background: url(../assets/list_sprite.png);
  background-size: cover;
}
.songlist {
  border-top: 1px solid rgba(0, 0, 0, 0.15);
  padding: 0 0 0 0.5rem;
  margin: 0;
}
.songlist li {
  font-size: 0.9rem;
  height: 3rem;
  box-sizing: border-box;
  border-bottom: 1px solid rgba(0, 0, 0, 0.15);
}
.songlist li h3 {
  font-size: 0.9rem;
  display: -webkit-box;
  font-weight: normal;
}
.songlist li p {
  font-size: 0.7rem;
  display: -webkit-box;
}
.albumList {
  overflow: hidden;
}
.albumList li {
  width: 32%;
  margin-right: 2%;
  font-size: 0;
  float: left;
}
.albumList li:last-child {
  margin-right: 0;
}
.albumList li img {
  width: 100%;
}
.albumList li .albumInfo {
  font-size: 0.6rem;
  padding: 0.4rem 0.3rem 0;
}
.albumList li .albumInfo p {
  color: gray;
}
.icon-point {
  display: inline-block;
  width: 2px;
  height: 2px;
  border-radius: 50%;
  background-color: gray;
  margin: 0 4px;
  vertical-align: middle;
}



